import { Injectable } from '@angular/core';
import { RouterService } from '@backbase/foundation-ang/core';
import { Observable, Subject } from 'rxjs';
import { Routes } from '../models';

@Injectable()
export class NavigationService {
  private readonly emitAfterSuccess = new Subject<void>();
  readonly afterSuccessEmitted$: Observable<void> =
    this.emitAfterSuccess.asObservable();

  constructor(private readonly routerService: RouterService) {}

  form(): Promise<boolean> {
    return this.routerService.navigate([Routes.FORM], {
      queryParamsHandling: 'merge',
    });
  }

  review(): Promise<boolean> {
    return this.routerService.navigate([Routes.REVIEW], {
      queryParamsHandling: 'merge',
    });
  }

  afterSuccess() {
    this.emitAfterSuccess.next();
  }
}
